import React from 'react';
import styled from 'styled-components';

const SytledButton = styled.button`
  width: 100%; padding: 15px 0 ; color: #fff; background:skyblue ; border-radius: 10px;
  border: none; &:hover{ background: #333}
`;

const Button = (props) => {
  return (
      <SytledButton {...props}/>      
  );
};

export default Button;