import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import Button from './Button';

const MemberFormBlock = styled.div`
  max-width: 500px; margin: 0 auto;
  h3 {text-align:center; font-size: 40px; margin: 30px auto;}
  
`;
const textMap = {
  login:'로그인',
  join: '회원가입'
}
const StyledInput = styled.input`
  padding:15px 10px; border-radius:10px; border:1px solid #555;
  width: 100%; margin-bottom:20px;
`;
const MemberFoot = styled.div`
  margin-top:20px; text-align:right;
  a {color:violet; text-decoration:underline;}
`;

const MemberForm = ({type}) => {
  const text = textMap[type] 
  return (
    <MemberFormBlock>
      <h3>{text}</h3>
      <form>
        <StyledInput name="userid" placeholder="아이디" type="text" />
        <StyledInput name="password" placeholder="비밀번호" type="password" />
        {
          type === 'join' && (
            <StyledInput name="passwordOk" placeholder="비밀번호확인" type="password" />
          )
        }
        <Button>{text}</Button>
      </form>
      <MemberFoot>
        {
          type === 'login' ? (
            <Link to='/join'>회원가입</Link>
          ) : (
            <Link to='/login'>로그인</Link>
          ) 
        }
      </MemberFoot>
    </MemberFormBlock>
  );
};

export default MemberForm;