import React, { useEffect } from 'react';
import Header from '../components/Header';
import Footer from '../components/Footer';
import styled from 'styled-components';

const NewsDetailBlock = styled.div`
  border-top: 80px solid #999;
  height:100vh;
  padding-top: 80px;
  .row { margin: 0 auto; text-align: center;
  p.tit {
    font-size: 32px; font-weight: 900; background: #eee;
    }
  p.imgBox{ 
    img {
      width : 40%; padding: 40px;
      }
    }
  p.text{
  font-size:20px
}
  }
`;

const NewsDetail = (props) => {
  // console.log(props);
  const { location, history} = props;

  useEffect (
    () => {
      if ( location.state === undefined) {
        history.push('/')
      }
    },[]
  )
  if (location.state) {
    return (
      <div className="wrap">
        <Header />
        <NewsDetailBlock>
          <div className="row">
            <p className="tit">{ location.state.title }</p>
            <p className="imgBox"><img src={location.state.urlToImage} alt={location.state.title} /></p>
            <p className="text">{ location.state.description }</p>
          </div>
        </NewsDetailBlock>
        <Footer />
      </div>
    );
  } else {
    return null;
  }
  
};

export default NewsDetail;