import React, { useState, useEffect } from 'react';
import axios from 'axios';
import NewsItem from './NewsItem';
//axios는 js 플러그인 데이터 불러오는
import styled from 'styled-components';
import { CgSearchLoading } from "react-icons/cg";


const NewsSectionBlock = styled.div`
  
  .row { padding-top: 80px; display:flex; flex-wrap:wrap; }
  .loadWrap { min-height: 800px; text-align: center; line-height: 800px;
    .loadicon {
      color: pink; font-size:500px; animation: loadspin infinite 5s linear;
    }
    @keyframes loadspin {
      from {transform: rotate(0deg); }
      to { transform: rotate(2turn); }
    }
  }
`;

const NewsSection = ({category}) => {
  const [articles, setArticles] = useState(null);
  const [loading, setLoading] = useState(false);

  const getNews = async () => {
    //setLoading(true)
    const query = category === 'all' ? '' : `&category=${category}` ; 
    const response = await axios.get(`http://newsapi.org/v2/top-headlines?country=kr${query}&apiKey=7f4b734ab0824c1d81a981d1ce099fd2`);
    //console.log(response)
    setArticles(response.data.articles);
    setLoading(true);
  }
  useEffect(
    () => {
      getNews()
    }, [category]
  )
  if (!loading) {
    return <NewsSectionBlock>
            <div className="loadWrap"> 
              <CgSearchLoading className="loadicon" />
            </div>
          </NewsSectionBlock>
  } else if(!articles) {
    return <NewsSectionBlock></NewsSectionBlock>;
  } else {
    return (
      <NewsSectionBlock>
        <div className="row">
          {
            articles.map( (article) => ( <NewsItem key={article.url} article={article} /> ) )
          }
        </div>
      </NewsSectionBlock>
    );
  }

  
};

export default NewsSection;